.layout--layoutPage {

  // Main sections are generally 100% wide, in order to implement full-bleed background.
  > .section {
    width: 100%;

    // Section inners is where we introduce max-widths if we want them.
    > .section--inner {
      max-width: $breakpoint4;
      margin: 0 auto;

      // Zone wrappers are where we introduce left/right padding.
      > .zone-wrapper {
        padding-left: 1em;
        padding-right: 1em;

        // Not sure what kinds of special things we should do with zones themselves.
        > .zone {
          &.zone-content {
            padding-top: 1em;
            padding-bottom: 1em;
          }
        }
      }
    }
  }
}

