}

.song.layout.wrapper {
  display: grid;
  grid-template-columns: 225px 4fr 1fr;
  grid-gap: 1.25em;
  grid-template-rows: auto 1fr;
  grid-template-areas: "a a a" "b b b" "c c c" "d d d" "e e e";
