.song.layout.wrapper {
  > div {
    padding: 0;
  }
}

.song.layout.wrapper {
  display: grid;
  grid-template-columns: $bandcamp-width__large 4fr 1fr;
  grid-gap: 1.25em;
  grid-template-rows: auto 1fr;

  grid-template-areas:
      "a a a"
      "b b b"
      "c c c"
      "d d d"
      "e e e";

  @media (min-width: $md-up) {
    grid-template-areas:
        "b a a"
        "b c c"
        "b d d"
        "e d d";
    grid-template-rows: auto auto auto 1fr;
  }

  @media (min-width: $lg-up) {
    grid-template-areas:
        "b a e"
        "b c e"
        "b d e";
    grid-template-rows: auto auto auto 1fr;
  }

  /*
  @media (min-width: $lg-up) {
    grid-template-areas:
        "b c d ";
  }
  */

  .title {
    grid-area: a;
  }

  .player {
    grid-area: b;
  }

  .personnel {
    grid-area: c;
  }

  .lyrics {
    grid-area: d;
  }

  .menu {
    grid-area: e;
  }

}
