.bandcamp-embed {
  overflow: hidden;
  display: block;
  text-align: center;
  margin: 0 auto;
  padding: 0;
  * {
    margin: 0;
    padding: 0;
  }
  &.small {
    display: block;
    width: $bandcamp-width__small !important;
    height: $bandcamp-height__small !important;
  }
  &.large {
    display: none;
    width: $bandcamp-width__large !important;
    height: $bandcamp-height__large !important;
  }

  @media (min-width: $md-up) {
    &.small {
      display: none;
    }
    &.large {
      display: block;
    }
  }

  margin-bottom: .5em;

}
