$header_height_factor: 30;

@mixin section_header_mixin($logo-height: $header_height_factor) {
  height: $logo-height;
  #block-mysteriousface-branding {
    height: $logo-height;
    width: $logo-height * 1.5;
    background-size: $logo-height * 2.5;
    background-position: 0 - ($logo-height * .25) 0 - ($logo-height * .375);
  }
}

#section_header {
  @include section_header_mixin($logo-height: (100vw * (math.div($header_height_factor, 100))));
  @media (min-width: $breakpoint3) {
    @include section_header_mixin($logo-height: $breakpoint3 * (math.div($header_height_factor, 100)));
  }
  background-color: #333;
  background-image: url(images/trees-background.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

#block-mysteriousface-branding {
  background-image: url(images/Isolated-Sign.png);
  background-repeat: no-repeat;
  a {
    display: block;
    height: inherit;
    width: inherit;
    background-color: transparent;
    text-indent: -9999px;
  }
}