body.home {
  background: url(./images/Final-Composite-NO-SIGNS-with-grain--blurred.jpg) no-repeat center center fixed;
  background-size: cover;
  color: white;

  a {
    color: white;
    opacity: .75;

    &:hover {
      opacity: 1.0;
    }
  }

  * {
    margin: 0;
  }

  // This is that new outer page container. Make it 100vh.
  .dialog-off-canvas-main-canvas {
    height: 100vh;
  }

  @media (min-width: $md-up) {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

// This is the outer container.  Center it as flex.
#page--home {
  display: block;
  margin: 0 auto;

  @media (min-width: $md-up) {
    width: 95vw;
    height: 95vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

// This is the inner container.
#home-page-items {
  // border: 1px dotted orange;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 90vw;
  grid-gap: 0;
  grid-template-areas:
    "image"
    "title"
    "menu"
    "body"
    "platforms";
  grid-template-rows: auto auto 1fr auto;

  @media (min-width: $md-up) {
    width: 88vw;
    grid-template-columns: 38vw 50vw;
    grid-template-areas:
      "image title"
      "image menu"
      "image body"
      "image platforms";
    grid-template-rows: min-content min-content 1fr min-content;
  }
}

.homepage-image-area,
.homepage-title-area,
.homepage-menu-area,
.homepage-body-area,
.homepage-platforms-area {
  display: block;
  // border: 1px dotted yellow;
  margin: 0;
  min-height: 10px;
  width: auto;
}

.homepage-image-area {
  grid-area: image;
  width: 38vw;
  height: 38vw;
  background: url(./images/album_cover.jpg);
  background-size: cover;
}

.homepage-title-area {
  grid-area: title;
  padding-left: 2vw;
}

.homepage-menu-area {
  grid-area: menu;
  padding-left: 2vw;
}

.homepage-body-area {
  grid-area: body;
  padding: 1vw 0 1vw 2vw;
}

.homepage-platforms-area {
  grid-area: platforms;
  padding-left: 2vw;
}
