.three-col.layout.wrapper {
  > div {
    padding: 0;
  }
}

.three-col.layout.wrapper {
  display: grid;
  grid-template-columns: $bandcamp-width__large 4fr 1fr;
  grid-gap: 1.25em;
  grid-template-rows: auto 1fr;

  grid-template-areas:
      "b b b "
      "c c c "
      "d d d";

  @media (min-width: $md-up) {
    grid-template-areas:
        "b c c "
        "d c c";
  }

  @media (min-width: $lg-up) {
    grid-template-areas:
        "b c d ";
  }

}

.sidebar_a {
  grid-area: b;
}

.main {
  grid-area: c;
}

.sidebar_b {
  grid-area: d;
}
