.homepage-menu-area {

  nav {
    ul {
      margin: 0;
      padding: 0;
      display: flex;
      li {
        margin: 0;
        padding: 0;
        display: block;
        a {
          color: white;
          text-decoration: none;
          display: block;

          border: .075em solid #999;
          font-size: 1.25rem;
          padding: .25em .5rem;
          margin-right: .5vw;
          margin-bottom: .5rem;

          @media (min-width: $md-up) {
            font-size: 1.75vw;
            border-width: .1vw;
            padding: .5vw .75vw;
            margin-right: .5vw;
            margin-bottom: 1vw;
          }

          font-weight: bold;
          line-height: 1;
          text-align: center;
          background: $dark-green;
          &:hover {
            background: $medium-green;
          }
          &.link-home {
            display: none;
          }
        }
      }
    }
  }

}
