.homepage-image-area {
  background: url(./images/album_cover.jpg);
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: 27%;
  background-size: cover;
  width: 100%;
  box-shadow: 0px 0px 6vw rgba(255, 255, 255, 0.35);
  margin-top: 1em;
  margin-bottom: 1em;

  @media (min-width: $md-up) {
    margin: 0;
  }
}

.homepage-title-area {
  h1 {
    font-size: 2.5rem;
    line-height: 1.125;
    margin: 0 0 .06rem;

    @media (min-width: $md-up) {
      font-size: 3vw;
      margin-bottom:  .6vw;
    }

    padding: 0;
  }
}

.homepage-body-area {
  font-size: 1.125rem;
  line-height: 1.5;
  margin-bottom: 1rem;

  @media (min-width: $md-up) {
    font-size: 1.4vw;
    margin-bottom: 0;
  }
}

video {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translateX(-50%) translateY(-50%);
  opacity: .1;
}
