@mixin base-font {
  font-family: $base-font-family;
  font-weight: normal;
}

@mixin heading-font {
  font-family: $heading-font-family;
  letter-spacing: 0;
  font-weight: bold;
  line-height: 1.1;
}

* {
  box-sizing: border-box;
}

/* Document level adjustments */
html {
  font-size: $base-font-size--max;
}

body {
  padding: 0;
  margin: 0;
  background-color: $white;
  color: $black;
  font-family: $base-font-family;
  line-height: $base-line-height;
  font-size: 1.125em;

  @media (min-width: $xs-up) {
    // font-size: 1.5em;
  }
  @media (min-width: $sm-up) {
    // font-size: 1.25em;
  }
  @media (min-width: $md-up) {
    // font-size: 1.125em;
  }
  @media (min-width: $lg-up) {
    font-size: 1em;
  }
}

h1, h2, h3, h4, p {
  margin: 0 0 .8em 0;
  padding: 0;
  text-decoration: none;
  a {
    text-decoration: none;
  }
}

h1, h2, h3, h4 {
  @include heading-font;
  font-weight: bold;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.75em;
}

h3 {
  font-size: 1.5em;
}

h4 {
  font-size: 1.25em;
}

a,
a:link {
  color: $deep-blue;
}

a:hover {
  color: $pale-blue;
}
