$main-menu--fontsize: 1.25rem;
$main-menu--lineheight: $main-menu--fontsize * 1.125;
$main-menu--padding-v: .5rem;
$main-menu--padding-h: .75rem;
$main-menu--sub-pos: ($main-menu--padding-v * 2) + $main-menu--lineheight;

$main-menu--bgcolor-main: #333;
$main-menu--bgcolor-main--hover: lighten($main-menu--bgcolor-main, 10%);

$main-menu--bgcolor-sub: $main-menu--bgcolor-main--hover;
$main-menu--bgcolor-sub--hover: lighten($main-menu--bgcolor-sub, 10%);

@mixin sf-menu--main-a {
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
  color: white;
  line-height: $main-menu--lineheight;
  font-size: $main-menu--fontsize;
  padding: $main-menu--padding-v $main-menu--padding-h;
  .sf-sub-indicator {
    display: none;
  }
}

ul.sf-menu.sf-main {

  a {
    @include sf-menu--main-a;
  }

  li {
    padding: 0;
    margin: 0;

    &.sf-depth-1 {
      background-color: #313;
      > ul {
        top: $main-menu--sub-pos;
      }
      > a {
        text-transform: uppercase;
        background-color: $main-menu--bgcolor-main;
        &:hover {
          background-color: $main-menu--bgcolor-main--hover;
        }
      }
    }

    &.sf-depth-2 {
      background-color: #525;
      > a {
        text-transform: none;
        background-color: $main-menu--bgcolor-sub;
        &:hover {
          background-color: $main-menu--bgcolor-sub--hover;
        }
      }
    }
  }
}

#block-mainnavigation {
  margin-top: 1em;
}

.sf-accordion-toggle {
  text-align: right;
  a#superfish-main-toggle {
    @include sf-menu--main-a;
    display: inline-block;
    border: 1px solid white;
    // font-size: 1.5em;
  }
}

#section_header .sf-menu.sf-main {
  display: block;
  @media (min-width: $md-up) {
    display: none;
  }
}
#section_menu {
  display: none;
  @media (min-width: $md-up) {
    display: block;
  }
}
